<template>
  <div v-if="showWelcome" class="welcome-container">
    <img src="/welcome.jpg" alt="" class="welcome-image" />
    <div class="welcome-content">
      <h1 class="welcome-title"></h1>
      <p class="welcome-subtitle"></p>
      <button class="welcome-button" @click="enterApp"></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'WelcomeScreen',
  data() {
    return {
      showWelcome: false
    }
  },
  mounted() {
    // 
    const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)
    // 
    const hasVisited = localStorage.getItem('hasVisitedApp')
    
    // 
    if (isMobile && !hasVisited) {
      this.showWelcome = true
      // 
      localStorage.setItem('hasVisitedApp', 'true')
    }
  },
  methods: {
    enterApp() {
      // 
      this.showWelcome = false
    }
  }
}
</script>

