<template>
  <div class="updates-container">
    <!--  -->
    <div class="page-header">
      <h1></h1>
      <p></p>
    </div>

    <!--  -->
    <div class="filter-section">
      <div class="filter-controls">
        <div class="filter-group">
          <label for="update-type">:</label>
          <select id="update-type" v-model="selectedType">
            <option value="all"></option>
            <option value="feature"></option>
            <option value="improvement"></option>
            <option value="bugfix">Bug</option>
          </select>
        </div>
        <div class="filter-group">
          <label for="date-filter">:</label>
          <select id="date-filter" v-model="sortOrder">
            <option value="newest"></option>
            <option value="oldest"></option>
          </select>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="updates-list">
      <div v-for="(update, index) in filteredUpdates" :key="index" class="update-card" :class="update.type">
        <div class="update-header">
          <div class="update-date">{{ formatDate(update.date) }}</div>
          <div class="update-badge" :class="update.type">{{ getTypeLabel(update.type) }}</div>
        </div>
        <div class="update-content">
          <h3 class="update-title">{{ update.title }}</h3>
          <div class="update-details">
            <ul v-if="update.details && update.details.length > 0">
              <li v-for="(detail, idx) in update.details" :key="idx">{{ detail }}</li>
            </ul>
            <p v-else>{{ update.description }}</p>
          </div>
        </div>
        <div class="update-footer">
          <div class="version-info"> {{ update.version }}</div>
          <div class="impact-badge" :class="update.impact">
            {{ getImpactLabel(update.impact) }}
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="pagination" v-if="paginatedUpdates.length > 0">
      <button 
        @click="currentPage--" 
        :disabled="currentPage === 1"
        class="page-btn"
      >
        
      </button>
      <div class="page-info">
         {{ currentPage }}  {{ totalPages }} 
      </div>
      <button 
        @click="currentPage++" 
        :disabled="currentPage === totalPages"
        class="page-btn"
      >
        
      </button>
    </div>

    <!--  -->
    <div class="empty-state" v-if="paginatedUpdates.length === 0">
      <p></p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Updates',
  data() {
    return {
      selectedType: 'all',
      sortOrder: 'newest',
      currentPage: 1,
      itemsPerPage: 5,
      updateRecords: [
        {
          id: 1,
          version: '1.2.5',
          date: '2023-06-15',
          title: '',
          type: 'improvement',
          impact: 'high',
          description: '',
          details: [
            '',
            '',
            '',
            ''
          ]
        },
        {
          id: 2,
          version: '1.2.4',
          date: '2023-05-30',
          title: '',
          type: 'feature',
          impact: 'medium',
          description: '',
          details: [
            '',
            '',
            '',
            ''
          ]
        },
        {
          id: 3,
          version: '1.2.3',
          date: '2023-05-10',
          title: '',
          type: 'bugfix',
          impact: 'critical',
          description: '',
          details: [
            '',
            '',
            ''
          ]
        },
        {
          id: 4,
          version: '1.2.2',
          date: '2023-04-25',
          title: '',
          type: 'improvement',
          impact: 'medium',
          description: '',
          details: [
            '',
            '',
            '',
            ''
          ]
        },
        {
          id: 5,
          version: '1.2.1',
          date: '2023-04-10',
          title: '',
          type: 'bugfix',
          impact: 'high',
          description: '',
          details: [
            '',
            'API',
            '',
            ''
          ]
        },
        {
          id: 6,
          version: '1.2.0',
          date: '2023-03-20',
          title: '',
          type: 'feature',
          impact: 'high',
          description: '',
          details: [
            '(RBAC)',
            '',
            '',
            ''
          ]
        },
        {
          id: 7,
          version: '1.1.5',
          date: '2023-02-28',
          title: '',
          type: 'improvement',
          impact: 'medium',
          description: '',
          details: [
            '',
            '',
            'HTML',
            ''
          ]
        },
        {
          id: 8,
          version: '1.1.4',
          date: '2023-02-10',
          title: '',
          type: 'bugfix',
          impact: 'medium',
          description: '',
          details: [
            '',
            'bug',
            ''
          ]
        },
        {
          id: 9,
          version: '1.1.3',
          date: '2023-01-20',
          title: 'SEO',
          type: 'feature',
          impact: 'medium',
          description: 'SEO',
          details: [
            '',
            '',
            '',
            'SEO'
          ]
        },
        {
          id: 10,
          version: '1.1.2',
          date: '2022-12-25',
          title: '',
          type: 'improvement',
          impact: 'low',
          description: '',
          details: [
            '',
            '',
            '',
            ''
          ]
        }
      ]
    }
  },
  computed: {
    // 
    filteredUpdates() {
      let result = [...this.updateRecords]
      
      // 
      if (this.selectedType !== 'all') {
        result = result.filter(update => update.type === this.selectedType)
      }
      
      // 
      result.sort((a, b) => {
        const dateA = new Date(a.date)
        const dateB = new Date(b.date)
        return this.sortOrder === 'newest' ? dateB - dateA : dateA - dateB
      })
      
      return result
    },
    
    // 
    paginatedUpdates() {
      const start = (this.currentPage - 1) * this.itemsPerPage
      const end = start + this.itemsPerPage
      return this.filteredUpdates.slice(start, end)
    },
    
    // 
    totalPages() {
      return Math.ceil(this.filteredUpdates.length / this.itemsPerPage)
    }
  },
  methods: {
    // 
    formatDate(dateString) {
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(dateString).toLocaleDateString('zh-CN', options)
    },
    
    // 
    getTypeLabel(type) {
      const labels = {
        'feature': '',
        'improvement': '',
        'bugfix': 'Bug'
      }
      return labels[type] || type
    },
    
    // 
    getImpactLabel(impact) {
      const labels = {
        'critical': '',
        'high': '',
        'medium': '',
        'low': ''
      }
      return labels[impact] || impact
    }
  },
  watch: {
    // 
    selectedType() {
      this.currentPage = 1
    },
    
    sortOrder() {
      this.currentPage = 1
    }
  }
}
</script>

