<template>
  <div class="topic-detail-container">
    <div class="topic-header">
      <button class="back-btn" @click="goBack">
         
      </button>
      <div class="topic-meta">
        <span class="category-tag">{{ topic.category || '' }}</span>
      </div>
    </div>
    
    <div class="topic-content">
      <h1 class="topic-title">{{ topic.title || '...' }}</h1>
      
      <div class="topic-stats">
        <span class="stat-item">
          <i class="icon-eye"></i>
          {{ formatNumber(topic.views || 0) }} 
        </span>
        <span class="stat-item">
          <i class="icon-comment"></i>
          {{ formatNumber(topic.comments || 0) }} 
        </span>
        <span class="stat-item">
          <i class="icon-time"></i>
          {{ formatDate(topic.updatedAt || new Date()) }}
        </span>
      </div>
      
      <div class="topic-body" v-if="!loading">
        <div v-if="loading" class="loading-content">
          <div class="loading-spinner"></div>
          <p>...</p>
        </div>
        
        <div v-else-if="topicNotFound" class="not-found">
          <h2></h2>
          <p></p>
          <button class="action-btn" @click="goBack"></button>
        </div>
        
        <div v-else>
          <!--  -->
          <div class="topic-text">
            <p>{{ topic.title }}</p>
            <p></p>
            <p v-if="topic.source">: {{ topic.source }}</p>
          </div>
          
          <!--  -->
          <div class="related-topics">
            <h3></h3>
            <div class="related-list">
              <div v-for="related in relatedTopics" :key="related.id" class="related-item" @click="navigateToTopic(related, $event)" :title="related.title">
                {{ related.title }}
              </div>
              <div v-if="relatedTopics.length === 0" class="no-related"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getHotTopics } from '../api/topics';

export default {
  name: 'TopicDetail',
  data() {
    return {
      topic: {},
      relatedTopics: [],
      loading: true,
      topicNotFound: false
    };
  },
  computed: {
    topicId() {
      return this.$route.params.id;
    }
  },
  async created() {
    await this.fetchTopicDetail();
    await this.fetchRelatedTopics();
  },
  methods: {
    async fetchTopicDetail() {
      try {
        this.loading = true;
        // API
        // 
        const response = await getHotTopics(20); // 
        const foundTopic = response.data.find(topic => topic.id === parseInt(this.topicId));
        
        if (foundTopic) {
          this.topic = foundTopic;
          // API
          this.topic.views += 1;
        } else {
          this.topicNotFound = true;
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
        this.topicNotFound = true;
      } finally {
        this.loading = false;
      }
    },
    
    async fetchRelatedTopics() {
      try {
        const response = await getHotTopics(5);
        // 
        this.relatedTopics = response.data.filter(topic => topic.id !== parseInt(this.topicId));
      } catch (error) {
        console.error(':', error);
      }
    },
   // 
    navigateToTopic(topic, event) {
      //  - event
      if (event && event.currentTarget && event.currentTarget.style) {
        event.currentTarget.style.transform = 'scale(0.98)';
        setTimeout(() => {
          if (event.currentTarget && event.currentTarget.style) {
            event.currentTarget.style.transform = '';
          }
        }, 100);
      }
      
      this.$router.push({
        path: `/topic/${topic.id}`,
        query: { title: topic.title, category: topic.category }
      });
    },
    
    goBack() {
      this.$router.back();
    },
    
    // 
    formatNumber(num) {
      if (num >= 10000) {
        return (num / 10000).toFixed(1) + 'w';
      } else if (num >= 1000) {
        return (num / 1000).toFixed(1) + 'k';
      }
      return num.toString();
    },
    
    // 
    formatDate(date) {
      const d = new Date(date);
      const now = new Date();
      const diffTime = Math.abs(now - d);
      const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
      
      if (diffDays === 0) {
        return '';
      } else if (diffDays === 1) {
        return '';
      } else if (diffDays < 7) {
        return `${diffDays}`;
      } else {
        return d.toLocaleDateString('zh-CN');
      }
    }
  }
};
</script>

