<template>
  <div class="auth-modal" v-if="visible" @click.self="handleClose">
    <div class="modal-content">
      <!--  -->
      <button class="close-btn" @click="handleClose">&times;</button>
      
      <!--  -->
      <div class="auth-tabs">
        <button 
          class="tab-btn" 
          :class="{ active: activeTab === 'login' }"
          @click="activeTab = 'login'"
        >
          
        </button>
        <button 
          class="tab-btn" 
          :class="{ active: activeTab === 'register' }"
          @click="activeTab = 'register'"
        >
          
        </button>
      </div>
      
      <!--  -->
      <div v-if="error" class="error-message">{{ error }}</div>
      
      <!--  -->
      <form v-if="activeTab === 'login'" @submit.prevent="handleLogin">
        <div class="form-group">
          <label for="login-username"></label>
          <input 
            type="text" 
            id="login-username" 
            v-model="loginForm.username" 
            required 
            placeholder=""
          />
        </div>
        <div class="form-group">
          <label for="login-password"></label>
          <input 
            type="password" 
            id="login-password" 
            v-model="loginForm.password" 
            required 
            placeholder=""
          />
        </div>
        <div class="form-group remember-me">
          <input 
            type="checkbox" 
            id="remember-me" 
            v-model="loginForm.rememberMe"
          />
          <label for="remember-me"></label>
        </div>
        <button type="submit" class="submit-btn" :disabled="loading">
          {{ loading ? '...' : '' }}
        </button>
      </form>
      
      <!--  -->
      <form v-if="activeTab === 'register'" @submit.prevent="handleRegister">
        <div class="form-group">
          <label for="register-username"></label>
          <input 
            type="text" 
            id="register-username" 
            v-model="registerForm.username" 
            required 
            placeholder=""
            minlength="3"
            maxlength="20"
          />
        </div>
        <div class="form-group">
          <label for="register-email"></label>
          <input 
            type="email" 
            id="register-email" 
            v-model="registerForm.email" 
            required 
            placeholder=""
          />
        </div>
        <div class="form-group">
          <label for="register-nickname"></label>
          <input 
            type="text" 
            id="register-nickname" 
            v-model="registerForm.nickname" 
            placeholder=""
            maxlength="20"
          />
        </div>
        <div class="form-group">
          <label for="register-password"></label>
          <input 
            type="password" 
            id="register-password" 
            v-model="registerForm.password" 
            required 
            placeholder="6"
            minlength="6"
          />
        </div>
        <div class="form-group">
          <label for="register-confirm"></label>
          <input 
            type="password" 
            id="register-confirm" 
            v-model="registerForm.confirmPassword" 
            required 
            placeholder=""
          />
        </div>
        <button type="submit" class="submit-btn" :disabled="loading">
          {{ loading ? '...' : '' }}
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'AuthModal',
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    initialTab: {
      type: String,
      default: 'login',
      validator: value => ['login', 'register'].includes(value)
    }
  },
  data() {
    return {
      activeTab: this.initialTab,
      loginForm: {
        username: '',
        password: '',
        rememberMe: false
      },
      registerForm: {
        username: '',
        email: '',
        nickname: '',
        password: '',
        confirmPassword: ''
      },
      error: ''
    }
  },
  computed: {
    ...mapState(['loading', 'rememberedUsername'])
  },
  watch: {
      visible(newVal) {
        if (newVal) {
          this.activeTab = this.initialTab
          this.clearForm()
          // 
          if (this.rememberedUsername && this.activeTab === 'login') {
            this.loginForm.username = this.rememberedUsername
            this.loginForm.rememberMe = true
          }
          this.error = ''
        }
      },
    initialTab(newVal) {
      if (this.visible) {
        this.activeTab = newVal
      }
    }
  },
  methods: {
    ...mapActions(['login', 'register']),
    
    /**
     * 
     */
    async handleLogin() {
      this.error = ''
      
      try {
        const result = await this.login(this.loginForm)
        if (result.success) {
          this.$emit('success', 'login', this.loginForm.username)
          this.handleClose()
        } else {
          this.error = result.error
        }
      } catch (err) {
        this.error = ''
        console.error(':', err)
      }
    },
    
    /**
     * 
     */
    async handleRegister() {
      this.error = ''
      
      // 
      if (this.registerForm.password !== this.registerForm.confirmPassword) {
        this.error = ''
        return
      }
      
      // 
      const registerData = {
        username: this.registerForm.username,
        email: this.registerForm.email,
        nickname: this.registerForm.nickname,
        password: this.registerForm.password
      }
      
      try {
        const result = await this.register(registerData)
        if (result.success) {
          // 
          this.error = ''
          this.activeTab = 'login'
          this.$emit('success', 'register', this.registerForm.username)
          
          // 
          alert('')
          
          // 
          this.clearRegisterForm()
        } else {
          this.error = result.error
        }
      } catch (err) {
        this.error = ''
        console.error(':', err)
      }
    },
    
    /**
     * 
     */
    handleClose() {
      this.clearForm()
      this.error = ''
      this.$emit('close')
    },
    
    /**
     * 
     */
    clearForm() {
      this.clearLoginForm()
      this.clearRegisterForm()
    },
    
    /**
     * 
     */
    clearLoginForm() {
      // 
      const rememberMe = this.loginForm.rememberMe
      this.loginForm = {
        username: '',
        password: '',
        rememberMe: rememberMe
      }
    },
    
    /**
     * 
     */
    clearRegisterForm() {
      this.registerForm = {
        username: '',
        email: '',
        nickname: '',
        password: '',
        confirmPassword: ''
      }
    }
  }
}
</script>

