<template>
  <nav class="navbar">
    <div class="navbar-container">
      <div class="navbar-brand">
        <router-link to="/"></router-link>
      </div>
      
      <!--  -->
      <button class="mobile-nav-toggle" @click="toggleMobileMenu">
        <span class="menu-icon"></span>
      </button>
      
      <!--  -->
      <div class="navbar-menu" :class="{ 'mobile-open': mobileMenuOpen }">
        <router-link to="/" class="nav-item" @click="closeMobileMenu"></router-link>
        <router-link to="/about" class="nav-item" @click="closeMobileMenu"></router-link>
        <router-link to="/updates" class="nav-item" @click="closeMobileMenu"></router-link>
        <router-link to="/category" class="nav-item" @click="closeMobileMenu"></router-link>
        <router-link to="/revenue" class="nav-item" @click="closeMobileMenu"></router-link>
        <router-link to="/music" class="nav-item" @click="closeMobileMenu"></router-link>
        
        <!--  -->
        <div class="auth-buttons" v-if="!isAuthenticated">
          <button class="auth-btn" @click="showAuthModal('login')"></button>
          <button class="auth-btn register" @click="showAuthModal('register')"></button>
        </div>
        
        <!--  -->
        <div class="user-menu" v-if="isAuthenticated">
          <div class="user-info" @click="handleUserInfoClick">
            <div class="user-avatar" :style="{ backgroundImage: `url(${currentUser?.avatar || '/default-avatar.png'})` }"></div>
            <span class="user-name">{{ currentUser?.nickname || currentUser?.username }}</span>
          </div>
        </div>
      </div>
    </div>
    
    <!-- / -->
    <AuthModal 
      :visible="authModalVisible" 
      :initial-tab="authModalTab"
      @close="closeAuthModal"
      @success="handleAuthSuccess"
    />
  </nav>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import AuthModal from './AuthModal.vue'

export default {
  name: 'NavBar',
  components: {
    AuthModal
  },
  data() {
    return {
      authModalVisible: false,
      authModalTab: 'login',
      userMenuOpen: false,
      mobileMenuOpen: false
    }
  },
  computed: {
    ...mapState(['isAuthenticated', 'currentUser'])
  },
  methods: {
    ...mapActions(['logout']),
    
    /**
     * 
     * @param {string} tab -  ('login'  'register')
     */
    showAuthModal(tab) {
      this.authModalTab = tab
      this.authModalVisible = true
      this.userMenuOpen = false
    },
    
    /**
     * 
     */
    closeAuthModal() {
      this.authModalVisible = false
    },
    
    /**
     * 
     * @param {string} type -  ('login'  'register')
     * @param {string} username - 
     */
    handleAuthSuccess(type, username) {
      console.log(`${type} :`, username)
      // 
      // 
      this.closeMobileMenu()
    },
    
    /**
     * 
     */
    toggleUserMenu() {
      // 
      const isMobile = window.innerWidth <= 768
      
      if (isMobile) {
        // 
        if (this.$route.path !== '/profile') {
          this.$router.push('/profile')
        }
        this.userMenuOpen = false
      } else {
        // 
        this.userMenuOpen = !this.userMenuOpen
        // 
        if (this.userMenuOpen) {
          this.mobileMenuOpen = false
        }
      }
    },
    
    /**
     * 
     */
    closeUserMenu() {
      this.userMenuOpen = false
    },
    
    /**
     * 
     */
    toggleMobileMenu() {
      this.mobileMenuOpen = !this.mobileMenuOpen
      // 
      if (this.mobileMenuOpen) {
        this.userMenuOpen = false
      }
    },
    
    /**
     * 
     */
    closeMobileMenu() {
      this.mobileMenuOpen = false
    },
    
    /**
     * 
     */
    async handleLogout() {
      try {
        await this.logout()
        this.userMenuOpen = false
        // 
        alert('')
      } catch (error) {
        console.error(':', error)
      }
    },
    
    /**
     * 
     */
    handleUserInfoClick() {
      // 
      this.closeMobileMenu()
      
      // 
      this.$router.push('/profile')
    }
  },
  mounted() {
    // 
    document.addEventListener('click', this.handleClickOutside)
  },
  beforeDestroy() {
    document.removeEventListener('click', this.handleClickOutside)
  },
  
  /**
   * 
   * @param {Event} event - 
   */
  handleClickOutside(event) {
    // 
    if (this.userMenuOpen && !event.target.closest('.user-menu')) {
      this.userMenuOpen = false
    }
    
    // 
    if (this.mobileMenuOpen && 
        !event.target.closest('.navbar-menu') && 
        !event.target.closest('.mobile-nav-toggle')) {
      this.mobileMenuOpen = false
    }
  }
}
</script>

