// 
function saveMusicState(state) {
  try {
    localStorage.setItem('music-play-state', JSON.stringify({
      currentSongIndex: state.currentSongIndex,
      likedSongs: state.likedSongs,
      isRepeating: state.isRepeating,
      isShuffling: state.isShuffling,
      volume: state.volume,
      isMuted: state.isMuted
    }))
  } catch (error) {
    console.error(':', error)
  }
}

// 
function loadMusicState() {
  try {
    const saved = localStorage.getItem('music-play-state')
    if (saved) {
      return JSON.parse(saved)
    }
  } catch (error) {
    console.error(':', error)
  }
  return {}
}

const savedMusicState = loadMusicState()

export default new Vuex.Store({
  state: {
    // 
    isAuthenticated: true, // 
    //  - 
    currentUser: {
      id: 1,
      username: 'admin',
      nickname: '',
      email: 'admin@example.com',
      avatar: 'https://picsum.photos/id/1005/200/200'
    },
    // 
    rememberedUsername: localStorage.getItem('rememberedUsername') || '',
    // 
    loading: false,
    // 
    error: null,
    
    // 
    isPlaying: false,
    currentTime: 0,
    duration: 0,
    volume: savedMusicState.volume || 80,
    isMuted: savedMusicState.isMuted || false,
    currentSongIndex: savedMusicState.currentSongIndex || -1,
    isRepeating: savedMusicState.isRepeating || false,
    isShuffling: savedMusicState.isShuffling || false,
    likedSongs: savedMusicState.likedSongs || [],
    songs: localSongs,
    playHistory: [],
    playTimer: null
  },
  
  mutations: {
    /**
     * 
     */
    SET_AUTHENTICATED(state, isAuthenticated) {
      state.isAuthenticated = isAuthenticated
    },
    
    /**
     * 
     */
    SET_CURRENT_USER(state, user) {
      state.currentUser = user
    },
    
    /**
     * 
     */
    SET_REMEMBERED_USERNAME(state, username) {
      state.rememberedUsername = username
    },
    
    /**
     * 
     */
    SET_LOADING(state, loading) {
      state.loading = loading
    },
    
    /**
     * 
     */
    SET_ERROR(state, error) {
      state.error = error
    },
    
    /**
     * 
     */
    CLEAR_ERROR(state) {
      state.error = null
    },
    
    /**
     * 
     */
    UPDATE_USER_INFO(state, userInfo) {
      if (state.currentUser) {
        state.currentUser = { ...state.currentUser, ...userInfo }
        // localStorage
        localStorage.setItem('user', JSON.stringify(state.currentUser))
        // 
        saveAuthState(true, state.currentUser)
      }
    },
    
    // mutations
    SET_PLAYING(state, isPlaying) {
      state.isPlaying = isPlaying
    },
    
    SET_CURRENT_TIME(state, time) {
      state.currentTime = time
    },
    
    SET_DURATION(state, duration) {
      state.duration = duration
    },
    
    SET_VOLUME(state, volume) {
      state.volume = volume
      saveMusicState(state)
    },
    
    SET_MUTED(state, isMuted) {
      state.isMuted = isMuted
      saveMusicState(state)
    },
    
    SET_CURRENT_SONG_INDEX(state, index) {
      state.currentSongIndex = index
      saveMusicState(state)
    },
    
    SET_REPEATING(state, isRepeating) {
      state.isRepeating = isRepeating
      saveMusicState(state)
    },
    
    SET_SHUFFLING(state, isShuffling) {
      state.isShuffling = isShuffling
      saveMusicState(state)
    },
    
    SET_LIKED_SONGS(state, songs) {
      state.likedSongs = songs
      saveMusicState(state)
    },
    
    ADD_TO_HISTORY(state, record) {
      // 
      state.playHistory.unshift(record)
      // 
      if (state.playHistory.length > 50) {
        state.playHistory = state.playHistory.slice(0, 50)
      }
      // 
      try {
        localStorage.setItem('music-play-history', JSON.stringify(state.playHistory))
      } catch (error) {
        console.error(':', error)
      }
    },
    
    CLEAR_HISTORY(state) {
      state.playHistory = []
      try {
        localStorage.removeItem('music-play-history')
      } catch (error) {
        console.error(':', error)
      }
    },
    
    SET_PLAY_HISTORY(state, history) {
      state.playHistory = history
    },
    
    SET_PLAY_TIMER(state, timer) {
      state.playTimer = timer
    }
  },
  
  actions: {
    /**
     * 
     */
    async login({ commit }, credentials) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        // credentialsrememberMe
        const { rememberMe, ...loginData } = credentials
        
        const response = await login(loginData)
        const { token, user } = response.data
        
        // tokenlocalStorage
        localStorage.setItem('token', token)
        localStorage.setItem('user', JSON.stringify(user))
        
        // 
        if (rememberMe) {
          localStorage.setItem('rememberedUsername', loginData.username)
        } else {
          localStorage.removeItem('rememberedUsername')
        }
        
        // 
        commit('SET_AUTHENTICATED', true)
        commit('SET_CURRENT_USER', user)
        
        // 
        saveAuthState(true, user)
        
        return { success: true }
      } catch (error) {
        commit('SET_ERROR', error.message || '')
        return { success: false, error: error.message || '' }
      } finally {
        commit('SET_LOADING', false)
      }
    },
    
    /**
     * 
     */
    async register({ commit }, userData) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        const response = await register(userData)
        return { success: true, data: response.data }
      } catch (error) {
        commit('SET_ERROR', error.message || '')
        return { success: false, error: error.message || '' }
      } finally {
        commit('SET_LOADING', false)
      }
    },
    
    /**
     * 
     */
    logout({ commit }) {
      // localStorage
      localStorage.removeItem('token')
      localStorage.removeItem('user')
      localStorage.removeItem('isAuthenticated')
      
      // 
      commit('SET_AUTHENTICATED', false)
      commit('SET_CURRENT_USER', null)
      
      // 
      saveAuthState(false)
      
      // 
      // rememberedUsername
    },
    
    /**
     * 
     */
    async fetchUserInfo({ commit, state }, userId) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        const response = await getUserInfo(userId || state.currentUser?.id)
        return { success: true, data: response.data }
      } catch (error) {
        commit('SET_ERROR', error.message || '')
        return { success: false, error: error.message || '' }
      } finally {
        commit('SET_LOADING', false)
      }
    },
    
    /**
     * 
     */
    async updateUserInfo({ commit, state }, userInfo) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        const response = await updateUserInfo(state.currentUser.id, userInfo)
        commit('UPDATE_USER_INFO', response.data)
        return { success: true, data: response.data }
      } catch (error) {
        commit('SET_ERROR', error.message || '')
        return { success: false, error: error.message || '' }
      } finally {
        commit('SET_LOADING', false)
      }
    },
    
    // actions
    // 
      initializeMusicState({ commit, dispatch }) {
        // localStorage
        const savedState = loadMusicState()
        if (savedState) {
          commit('SET_CURRENT_SONG_INDEX', savedState.currentSongIndex || -1)
          commit('SET_CURRENT_TIME', savedState.currentTime || 0)
          commit('SET_DURATION', savedState.duration || 0)
          commit('SET_VOLUME', savedState.volume || 80)
          commit('SET_MUTED', savedState.isMuted || false)
      commit('SET_REPEATING', savedState.isRepeating || false)
      commit('SET_SHUFFLING', savedState.isShuffling || false)
          commit('SET_LIKED_SONGS', savedState.likedSongs || [])
        }
        
        // 
        dispatch('loadPlayHistory')
      },
    
    togglePlay({ commit, state }, audioElement = null) {
      if (state.songs.length === 0) return
      
      if (state.currentSongIndex === -1) {
        // 
        return this.dispatch('playSong', 0)
      } else {
        if (state.isPlaying) {
          // 
          if (audioElement) {
            audioElement.pause()
          }
          commit('SET_PLAYING', false)
          // 
          if (state.playTimer) {
            clearInterval(state.playTimer)
            commit('SET_PLAY_TIMER', null)
          }
        } else {
          // 
          commit('SET_PLAYING', true)
          if (audioElement) {
            audioElement.play().catch(error => {
              console.error(':', error)
              // 
              this.dispatch('startPlaySimulation')
            })
          } else {
            // 
            this.dispatch('startPlaySimulation')
          }
        }
        return state.isPlaying
      }
    },
    
    playSong({ commit, state, dispatch }, index) {
      if (index < 0 || index >= state.songs.length) return
      
      commit('SET_CURRENT_SONG_INDEX', index)
      commit('SET_CURRENT_TIME', 0)
      commit('SET_DURATION', state.songs[index].duration)
      commit('SET_PLAYING', true)
      
      // 
      const record = {
        songIndex: index,
        song: state.songs[index],
        timestamp: new Date().toISOString()
      }
      commit('ADD_TO_HISTORY', record)
      
      // 
      if (state.playTimer) {
        clearInterval(state.playTimer)
        commit('SET_PLAY_TIMER', null)
      }
      
      return state.songs[index]
    },
    
    playPrevious({ commit, state, dispatch }) {
      if (state.songs.length === 0) return
      
      const newIndex = state.currentSongIndex > 0 
        ? state.currentSongIndex - 1 
        : state.songs.length - 1
      return dispatch('playSong', newIndex)
    },
    
    playNext({ commit, state, dispatch }) {
      if (state.songs.length === 0) return
      
      if (state.isShuffling) {
        // 
        let nextIndex
        do {
          nextIndex = Math.floor(Math.random() * state.songs.length)
        } while (nextIndex === state.currentSongIndex && state.songs.length > 1)
        return dispatch('playSong', nextIndex)
      } else {
        // 
        const newIndex = state.currentSongIndex < state.songs.length - 1 
          ? state.currentSongIndex + 1 
          : 0
        return dispatch('playSong', newIndex)
      }
    },
    
    toggleRepeat({ commit, state }) {
      commit('SET_REPEATING', !state.isRepeating)
      return !state.isRepeating
    },
    
    toggleShuffle({ commit, state }) {
      commit('SET_SHUFFLING', !state.isShuffling)
      return !state.isShuffling
    },
    
    toggleLike({ commit, state }) {
      if (state.currentSongIndex === -1) return
      
      const songId = state.songs[state.currentSongIndex].id
      let newLikedSongs
      
      if (state.likedSongs.includes(songId)) {
        newLikedSongs = state.likedSongs.filter(id => id !== songId)
      } else {
        newLikedSongs = [...state.likedSongs, songId]
      }
      
      commit('SET_LIKED_SONGS', newLikedSongs)
      return newLikedSongs.includes(songId)
    },
    
    updateProgress({ commit, state }, progress) {
      const newTime = (progress / 100) * state.duration
      commit('SET_CURRENT_TIME', newTime)
      return newTime
    },
    
    toggleMute({ commit, state }) {
      commit('SET_MUTED', !state.isMuted)
      return !state.isMuted
    },
    
    updateVolume({ commit }, volume) {
      commit('SET_VOLUME', volume)
    },
    
    updateAudioProgress({ commit }, time) {
      commit('SET_CURRENT_TIME', time)
    },
    
    updateAudioDuration({ commit }, duration) {
      commit('SET_DURATION', duration)
    },
    
    handleAudioEnd({ commit, state, dispatch }) {
      if (state.isRepeating) {
        // 
        commit('SET_CURRENT_TIME', 0)
        return dispatch('togglePlay')
      } else {
        // 
        return dispatch('playNext')
      }
    },
    
    // 
    handleAudioTimeUpdate({ commit }, currentTime) {
      commit('SET_CURRENT_TIME', currentTime)
    },
    
    // 
    handleAudioEnded({ commit, state, dispatch }) {
      if (state.isRepeating) {
        // 
        commit('SET_CURRENT_TIME', 0)
        // 
        dispatch('togglePlay')
      } else {
        // 
        dispatch('playNext')
      }
    },
    
    // 
    handleAudioLoadedMetadata({ commit }, duration) {
      commit('SET_DURATION', duration)
    },
    
    startPlaySimulation({ commit, state }) {
      // 
      if (state.playTimer) {
        clearInterval(state.playTimer)
      }
      
      const timer = setInterval(() => {
        if (state.isPlaying) {
          const newTime = state.currentTime + 1
          
          // 
          if (newTime >= state.duration) {
            clearInterval(timer)
            commit('SET_PLAY_TIMER', null)
            
            if (state.isRepeating) {
              // 
              commit('SET_CURRENT_TIME', 0)
              this.dispatch('startPlaySimulation')
            } else {
              // 
              this.dispatch('playNext')
            }
          } else {
            commit('SET_CURRENT_TIME', newTime)
          }
        }
      }, 1000)
      
      commit('SET_PLAY_TIMER', timer)
    },
    
    loadPlayHistory({ commit }) {
      try {
        const history = localStorage.getItem('music-play-history')
        if (history) {
          commit('SET_PLAY_HISTORY', JSON.parse(history))
        }
      } catch (error) {
        console.error(':', error)
        commit('SET_PLAY_HISTORY', [])
      }
    },
    
    clearAllHistory({ commit }) {
      commit('CLEAR_HISTORY')
    },
    
    removeFromHistory({ commit, state }, index) {
      const newHistory = [...state.playHistory]
      newHistory.splice(index, 1)
      commit('SET_PLAY_HISTORY', newHistory)
      // localStorage
      try {
        localStorage.setItem('music-play-history', JSON.stringify(newHistory))
      } catch (error) {
        console.error(':', error)
      }
    }
  },
  
  getters: {
    /**
     * 
     */
    isAuthenticated: state => state.isAuthenticated,
    
    /**
     * 
     */
    currentUser: state => state.currentUser,
    
    /**
     * 
     */
    isLoading: state => state.loading,
    
    /**
     * 
     */
    error: state => state.error,
    
    /**
     * 
     */
    userAvatar: state => state.currentUser?.avatar || '/default-avatar.png',
    
    /**
     * 
     */
    userDisplayName: state => state.currentUser?.nickname || state.currentUser?.username || '',
    
    // getters
    currentSong: state => state.currentSongIndex >= 0 && state.currentSongIndex < state.songs.length 
      ? state.songs[state.currentSongIndex] 
      : null,
    
    hasSongs: state => state.songs.length > 0,
    
    progress: state => state.duration > 0 ? (state.currentTime / state.duration) * 100 : 0,
    
    isCurrentSongLiked: state => {
      if (state.currentSongIndex === -1) return false
      const songId = state.songs[state.currentSongIndex].id
      return state.likedSongs.includes(songId)
    }
  }
})