// 
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

// 
export const getCategories = async () => {
  if (USE_MOCK_DATA) {
    await delay(200);
    return { data: mockCategories };
  }
  return api.get('/categories');
};

// 
export const getTopicsByCategory = async (categoryId) => {
  if (USE_MOCK_DATA) {
    await delay(300);
    const category = mockCategories.find(cat => cat.id === categoryId);
    const topics = mockHotTopics.filter(topic => topic.category === category?.name);
    return { data: topics };
  }
  return api.get(`/topics/category/${categoryId}`);
};

// 
export const getHotTopics = async (limit = 10) => {
  if (USE_MOCK_DATA) {
    await delay(300);
    return { data: mockHotTopics.slice(0, limit) };
  }
  return api.get(`/topics/hot?limit=${limit}`);
};

// 
export const searchTopics = async (keyword) => {
  if (USE_MOCK_DATA) {
    await delay(300);
    const results = mockHotTopics.filter(topic => 
      topic.title.includes(keyword)
    );
    return { data: results };
  }
  return api.get(`/topics/search?keyword=${keyword}`);
};

export default {
  getCategories,
  getTopicsByCategory,
  getHotTopics,
  searchTopics
};