import Vue from 'vue'
import VueRouter from 'vue-router'
import TopicDetail from '../views/TopicDetail.vue'
import Updates from '../pages/Updates.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../pages/Home.vue')
  },
  {
    path: '/music',
    name: 'MusicPlayer',
    component: () => import('../pages/MusicPlayer.vue')
  },
  {
    path: '/welcome',
    name: 'Welcome',
    component: () => import('../views/WelcomeAnimation.vue')
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('../pages/About.vue')
  },
  {
    path: '/topic/:id',
    name: 'TopicDetail',
    component: TopicDetail,
    props: true
  },
  {
    path: '/blog/:id',
    name: 'BlogDetail',
    component: () => import('../pages/BlogDetail.vue')
  },
  {
    path: '/profile',
    name: 'Profile',
    component: () => import('../pages/ProfilePage.vue'),
    meta: {
      requiresAuth: true // 
    }
  },
  {
    path: '/category/:id?',
    name: 'Category',
    component: () => import('../pages/CategoryPage.vue')
  },
  {
    path: '/revenue',
    name: 'RevenueChart',
    component: () => import('../pages/RevenueChart.vue')
  },
  {
    path: '/updates',
    name: 'Updates',
    component: Updates
  },
  { path: '*', redirect: '/' },
  // 
  { 
    path: '/pixel-test', 
    name: 'PixelTest', 
    component: () => import('../views/PixelTest.vue') 
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router