// 
const USE_MOCK_DATA = true;

// axios
const api = axios.create({
  baseURL: 'http://localhost:8080/api',
  timeout: 10000
})

// API
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

// 
export const getBlogList = async (page = 0, size = 10, sort = 'latest') => {
  if (USE_MOCK_DATA) {
    await delay(300); // 
    
    // 
    let sortedBlogs = [...mockBlogs];
    switch (sort) {
      case 'hot':
        // 
        sortedBlogs.sort((a, b) => (b.views || 0) - (a.views || 0));
        break;
      case 'likes':
        // 
        sortedBlogs.sort((a, b) => (b.likeCount || 0) - (a.likeCount || 0));
        break;
      case 'latest':
      default:
        // 
        sortedBlogs.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        break;
    }
    
    // 
    const start = page * size
    const end = start + size
    const paginated = sortedBlogs.slice(start, end)
    
    // Page
    return {
      data: {
        content: paginated,
        totalPages: Math.ceil(sortedBlogs.length / size),
        totalElements: sortedBlogs.length,
        number: page,
        size: size
      }
    };
  }
  return api.get('/blogs', {
    params: { page, size, sort }
  });
};

// 
export const getBlogDetail = async (id) => {
  if (USE_MOCK_DATA) {
    await delay(200);
    const blog = mockBlogs.find(b => b.id === parseInt(id));
    return { data: blog || null };
  }
  return api.get(`/blogs/${id}`);
};

// 
export const createBlog = async (blog) => {
  if (USE_MOCK_DATA) {
    await delay(400);
    const newBlog = {
      id: Date.now(),
      ...blog,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    };
    mockBlogs.unshift(newBlog);
    return { data: newBlog };
  }
  return api.post('/blogs', blog);
};

// 
export const updateBlog = async (id, blog) => {
  if (USE_MOCK_DATA) {
    await delay(300);
    const index = mockBlogs.findIndex(b => b.id === parseInt(id));
    if (index !== -1) {
      mockBlogs[index] = {
        ...mockBlogs[index],
        ...blog,
        updatedAt: new Date().toISOString()
      };
      return { data: mockBlogs[index] };
    }
    return { data: null };
  }
  return api.put(`/blogs/${id}`, blog);
};

// 
export const deleteBlog = async (id) => {
  if (USE_MOCK_DATA) {
    await delay(200);
    const index = mockBlogs.findIndex(b => b.id === parseInt(id));
    if (index !== -1) {
      mockBlogs.splice(index, 1);
    }
    return { data: { success: true } };
  }
  return api.delete(`/blogs/${id}`);
};

// 
export const searchBlogs = async (keyword, page = 0, size = 10, sort = 'latest') => {
  if (USE_MOCK_DATA) {
    await delay(300);
    const filtered = mockBlogs.filter(blog => 
      blog.title.includes(keyword) || blog.content.includes(keyword)
    );
    
    // 
    let sortedBlogs = [...filtered];
    switch (sort) {
      case 'hot':
        // 
        sortedBlogs.sort((a, b) => (b.views || 0) - (a.views || 0));
        break;
      case 'likes':
        // 
        sortedBlogs.sort((a, b) => (b.likeCount || 0) - (a.likeCount || 0));
        break;
      case 'latest':
      default:
        // 
        sortedBlogs.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        break;
    }
    
    // 
    const start = page * size
    const end = start + size
    const paginated = sortedBlogs.slice(start, end)
    
    // Page
    return {
      data: {
        content: paginated,
        totalPages: Math.ceil(sortedBlogs.length / size),
        totalElements: sortedBlogs.length,
        number: page,
        size: size
      }
    };
  }
  return api.get('/blogs/search', {
    params: { keyword, page, size, sort }
  });
};

export default {
  getBlogList,
  getBlogDetail,
  createBlog,
  updateBlog,
  deleteBlog,
  searchBlogs,
  mockBlogs
};