<template>
  <div id="app">
    <!--  -  -->
    <audio 
      ref="globalAudioPlayer" 
      :src="currentSong?.src" 
      @timeupdate="updateAudioProgress" 
      @loadedmetadata="updateAudioDuration" 
      @ended="handleAudioEnd"
      :volume="isMuted ? 0 : volume / 100"
      @play="handlePlay"
      @pause="handlePause"
    ></audio>
    
    <WelcomeScreen />
    <NavBar/>
    <main class="main-content">
      <router-view/>
    </main>
  </div>
</template>

<script>
import NavBar from './components/NavBar.vue';
import WelcomeScreen from './components/WelcomeScreen.vue';
import { mapState, mapGetters } from 'vuex';

export default {
  name: 'App',
  components: {
    NavBar,
    WelcomeScreen
  },
  computed: {
    ...mapState([
      'isPlaying',
      'currentTime',
      'duration',
      'volume',
      'isMuted',
      'currentSongIndex'
    ]),
    ...mapGetters([
      'currentSong'
    ])
  },
  watch: {
    // 
    currentSong(newSong) {
      if (newSong && this.$refs.globalAudioPlayer) {
        const audio = this.$refs.globalAudioPlayer;
        audio.currentTime = 0;
        audio.load();
        // 
        if (this.isPlaying) {
          audio.play().catch(err => {
            console.error(':', err);
            // store
            this.$store.commit('SET_PLAYING', false);
          });
        }
      }
    },
    // 
    isPlaying(playing) {
      if (this.$refs.globalAudioPlayer) {
        if (playing) {
          this.$refs.globalAudioPlayer.play().catch(err => {
            console.error(':', err);
            // store
            this.$store.commit('SET_PLAYING', false);
          });
        } else {
          this.$refs.globalAudioPlayer.pause();
        }
      }
    },
    // 
    volume(newVolume) {
      if (this.$refs.globalAudioPlayer) {
        this.$refs.globalAudioPlayer.volume = this.isMuted ? 0 : newVolume / 100;
      }
    },
    // 
    isMuted(muted) {
      if (this.$refs.globalAudioPlayer) {
        this.$refs.globalAudioPlayer.muted = muted;
      }
    },
    // 
    currentTime(newTime) {
      if (this.$refs.globalAudioPlayer && Math.abs(this.$refs.globalAudioPlayer.currentTime - newTime) > 1) {
        this.$refs.globalAudioPlayer.currentTime = newTime;
      }
    }
  },
  mounted() {
    // 
    this.$nextTick(() => {
      if (this.isPlaying && this.currentSong && this.$refs.globalAudioPlayer) {
        this.$refs.globalAudioPlayer.play().catch(err => {
          console.error(':', err);
        });
      }
    });
  },
  methods: {
    // 
    updateAudioProgress(event) {
      this.$store.dispatch('updateAudioProgress', event.target.currentTime);
    },
    // 
    updateAudioDuration(event) {
      this.$store.dispatch('updateAudioDuration', event.target.duration);
    },
    // 
    handleAudioEnd() {
      // 
      this.$store.commit('SET_PLAYING', false);
      // action
      this.$store.dispatch('handleAudioEnd');
    },
    // 
    handlePlay() {
      this.$store.commit('SET_PLAYING', true);
    },
    // 
    handlePause() {
      this.$store.commit('SET_PLAYING', false);
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  min-height: 100vh;
}

.main-content {
  padding-top: 80px;
  min-height: calc(100vh - 80px);
}
</style>