import axios from 'axios'

// 
const mockUsers = [
  {
    id: 1,
    username: 'admin',
    password: '123456', // 
    email: 'admin@example.com',
    nickname: '',
    avatar: 'https://picsum.photos/id/1005/200/200',
    birthday: '1990-01-01',
    bio: '',
    createdAt: '2024-01-01T00:00:00'
  }
];

// 
const USE_MOCK_DATA = true;

// axios
const api = axios.create({
  baseURL: 'http://localhost:8080/api',
  timeout: 10000
})

// API
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

/**
 * 
 * @param {Object} credentials - usernamepassword
 * @returns {Promise} 
 */
export const login = async (credentials) => {
  if (USE_MOCK_DATA) {
    await delay(500);
    const user = mockUsers.find(u => 
      u.username === credentials.username && 
      u.password === credentials.password
    );
    
    if (user) {
      // token
      const token = 'mock-jwt-token-' + Date.now();
      return {
        data: {
          token,
          user: {
            id: user.id,
            username: user.username,
            nickname: user.nickname,
            avatar: user.avatar,
            email: user.email
          }
        }
      };
    }
    throw new Error('');
  }
  return api.post('/auth/login', credentials);
};

/**
 * 
 * @param {Object} userData - 
 * @returns {Promise} 
 */
export const register = async (userData) => {
  if (USE_MOCK_DATA) {
    await delay(500);
    
    // 
    const existingUser = mockUsers.find(u => u.username === userData.username);
    if (existingUser) {
      throw new Error('');
    }
    
    // 
    const existingEmail = mockUsers.find(u => u.email === userData.email);
    if (existingEmail) {
      throw new Error('');
    }
    
    // 
    const newUser = {
      id: Date.now(),
      username: userData.username,
      password: userData.password, // 
      email: userData.email,
      nickname: userData.nickname || userData.username,
      avatar: 'https://picsum.photos/id/1012/200/200', // 
      birthday: userData.birthday || null,
      bio: userData.bio || '',
      createdAt: new Date().toISOString()
    };
    
    mockUsers.push(newUser);
    
    // 
    return {
      data: {
        message: '',
        user: {
          id: newUser.id,
          username: newUser.username,
          nickname: newUser.nickname,
          avatar: newUser.avatar,
          email: newUser.email
        }
      }
    };
  }
  return api.post('/auth/register', userData);
};

/**
 * 
 * @param {number} userId - ID
 * @returns {Promise} 
 */
export const getUserInfo = async (userId) => {
  if (USE_MOCK_DATA) {
    await delay(300);
    const user = mockUsers.find(u => u.id === parseInt(userId));
    if (!user) {
      throw new Error('');
    }
    
    // 
    return {
      data: {
        id: user.id,
        username: user.username,
        nickname: user.nickname,
        avatar: user.avatar,
        email: user.email,
        birthday: user.birthday,
        bio: user.bio,
        createdAt: user.createdAt
      }
    };
  }
  return api.get(`/users/${userId}`);
};

/**
 * 
 * @param {number} userId - ID
 * @param {Object} userData - 
 * @returns {Promise} 
 */
export const updateUserInfo = async (userId, userData) => {
  if (USE_MOCK_DATA) {
    await delay(400);
    const userIndex = mockUsers.findIndex(u => u.id === parseInt(userId));
    
    if (userIndex === -1) {
      throw new Error('');
    }
    
    // 
    mockUsers[userIndex] = {
      ...mockUsers[userIndex],
      ...userData,
      password: mockUsers[userIndex].password // 
    };
    
    const updatedUser = mockUsers[userIndex];
    
    // 
    return {
      data: {
        id: updatedUser.id,
        username: updatedUser.username,
        nickname: updatedUser.nickname,
        avatar: updatedUser.avatar,
        email: updatedUser.email,
        birthday: updatedUser.birthday,
        bio: updatedUser.bio
      }
    };
  }
  return api.put(`/users/${userId}`, userData);
};

export default api;
